@import url('https://fonts.googleapis.com/css2?family=Crimson+Text&family=Kanit:wght@200&family=Lato:ital,wght@0,300;0,700;1,700&family=Montserrat:ital,wght@0,300;1,600&family=Pangolin&family=Roboto:ital,wght@0,300;1,700;1,900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Crimson+Text:ital@0;1&family=Kanit:wght@200&family=Lato:ital,wght@0,300;0,700;1,700&family=Montserrat:ital,wght@0,300;1,600&family=Pangolin&family=Roboto:ital,wght@0,300;1,700;1,900&display=swap');

.header{
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: black;
  color: white;
  top: 0;
  position: sticky;
  z-index: 2000;

}
.header-left{
  flex: 0.4;

}
.header-left >h1{
  text-align: center;
  font-family: crimson test;
  letter-spacing: 1px;
  padding: 20px 0;
}
.header-left >h1 >span{
  color: #f64c08;


}
.header-right{
  display: flex;
  flex: 0.6;
  justify-content: space-between;
  align-items: center;
}
.header-right >a >h4{
  font-size: 18px;
  cursor: pointer;
  transition: transfrom 200ms ease-in-out;
  padding: 20px 0;
  letter-spacing: 0px;
}
.header-right > a > h4:hover{
  transition: scale(1.09);
  background-color:#f64c08 ;
  color: white;
  padding: 5px 20px;
  border-radius: 2px;
  letter-spacing: 1px;
  font-family: crimson test,serif;
  font-size: 18px;
  cursor: pointer;
}
.header-rightButton{
  background-color: #f64c08;
  color: white;
  padding: 5px 20px;
  border-radius:2px;
  cursor: pointer;
}