@import url('https://fonts.googleapis.com/css2?family=Crimson+Text:ital@0;1&family=Kanit:wght@200&family=Lato:ital,wght@0,300;0,700;1,700&family=Montserrat:ital,wght@0,300;1,200;1,600&family=Pangolin&family=Roboto:ital,wght@0,300;1,700;1,900&display=swap');

.skillcontainer{
  display: flex;
  align-items: center;
  justify-content: center;
  margin:200px 0;
}
.skillcontainer-text{
  color: white;
  flex: 0.6;
  text-align: left;
  max-width: 500px;
  margin-bottom: 20px;

}
.skillcontainer-text > h2{
  font-size: 30px;
}
.skillcontainer-image{
  flex:0.4;
  text-align: center;
  margin-right: 100px;
}
.skillcontainer-image > img {
  width: 600px;
  border-radius: 3px;
  object-fit: contain;
}
.skillcontainer-skillset > h5{
  font-family: "Montserrat",sans-serif;
  font-weight: 300;
  padding-bottom: 30px;
  padding-top: 10px;

}
.skillcontainer-slider > .MuiLinearprogress-colorprimary{
  background-color: rgb(88, 88,88);

}
.skillcontainer-slider1 >div > .MuiLinearprogress-barColorprimary{
background-color: tomato;
}

