.contact{
  display:flex;
  flex-direction: column;
  color: white;
  justify-content: center;
 margin: 200px 0 100px;
  align-items: center;
}
.contact-container{
  border: 1px solid white;
  width: 800px;
  height: 400px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px;
  margin-top: 20px;

}
.contact-container >p{
  font-size: 20px;
  text-align: left;
  padding-bottom: 30px;

}
.contact-container >p> span{
  color: #f65c08;
}
.contact-icons >a >button >span >MuiSvgIcon-root{
  color: white;
  font-size: 30px;
}
.contact-icons  > a> button{
  color: white;
}