.topContent{
  color: white;
  position: relative;

  
  }
  .topContent-container{
    
    position: absolute;
    top: 250px;
    right: 350px;

  }
  .topContent-container >p{
 letter-spacing: 1px;
 text-align: left;
 padding-left: 3px;
 padding-top: 20px;
 font-weight:300px;
 cursor: pointer;
 color: white;
 

 
  }
  .topContent-container> a> button{
    margin-bottom: 30px;
    text-align: left;
    margin-left: 5px;
    border: none;
    border-radius: 2px;
    outline: none;

  }
  .topContent-container > h1 {
    font-size: 50px;
    font-family: "montserrat", sans-serif;
  }
 .topcontent-resume{
  color: white;
  background-color: #f64c08;
  font-size: 14px;
  padding: 10px 30px;
  font-family: "montserrat",sans-serif;
font-weight: 300;
cursor: pointer;
margin-right: 20px;
 }
.topcontent-mywork{
  color: white;
  border: 1px solid white !important;
  background-color: transparent;
  font-size: 14px;
  padding: 9px 45px;
  font-family: "Montserrat";
  font-weight: 300;
  cursor: pointer;
} 
.topcontent-resume:hover{
  color: #f64c08;
