.project >img {
  width: 350px;
  height: 179px;
}
.project{
  margin:15px ;
  width: 350px;
  height: 179px;
  transition: transform 100ms ease-in;
}
.project:hover{
  color: white;
  background-color: #f64cf6;
  transform: scale(1.05);
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.project-content{
  padding: 10px;
}
.project-content > h4 {
  padding-bottom: 5px;
}
.project-content >p{
  font-size: 15px;

}