.experiencecontainer{
  display: flex;
  flex-direction: column;
  color: white;
  justify-content: center;
  
  align-items: center;
}

.experiencecontainer-info{
  background: url("../../Images/images.webp");
  background-position: center;
  background-size: cover;
  height: 300px;
  z-index: 1000px;
  width: 100vh;
  box-shadow: inset 0 0 0 2000px rgba(0,0,0,0.9);
  display: flex;
  justify-content: space-evenly;
  text-align: center;
  gap: 50px;
}
